import React from 'react'
import s from './DateTimer.module.css'



class DateTimer extends React.Component {
  constructor(props){
    super(props)
    this.state = {hours:"--", minutes:"--", seconds:"--", days:"--", months:"--", years:"--"}
    
  }

  getDateTime(){
    const date = new Date()
    const hours = date.getHours()
    const minutes = date.getMinutes()
    const seconds = date.getSeconds()
    const days = date.getDate()
    const months = date.getMonth()
    const years = date.getFullYear()

    this.setState((prevState)=>{
      return {
        hours:`${hours}`,
        minutes: `${minutes}`,
        seconds: `${seconds}`,
        days: `${days}`,
        months: `${months}`,
        years: `${years}`
      }
    }, this.timeOut())
    
  }

  timeOut(){
    setTimeout(()=>{
      this.getDateTime()
    },1000)
  }

  componentDidMount(){
    this.timeOut()
  }

  render(){
    
    return (
      
      <ul className={s['metaData']}>
        <li>
          <span className={s['timeUnit']}>{this.state.days}</span> :
          <span className={s['timeUnit']}>{this.state.months}</span> :
          <span className={s['timeUnit']}>{this.state.years}</span>

        </li>
        <li>
          <span className={s['timeUnit']}>{this.state.hours}</span> : 
          <span className={s['timeUnit']}>{this.state.minutes}</span> : 
          <span className={s['timeUnit']}>{this.state.seconds}</span>
        </li>
      </ul>
        
    )
  }
}

export default DateTimer;


